@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario {
    display: flex;
    flex-direction: column;

    &__fieldset {
        border: none;
        margin-top: 1rem;
        padding: 0;
    }

    &__legend {
        color: v.$gris;
        font-size: 2.2rem;
        margin-bottom: 2rem;
    }
    
    &__campo {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        margin: 1.5rem 0;
    }

    &__label {
        display: block;
        font-size: 1.8rem;
        color: rgba(v.$negro, .8);
    }

    &__input,
    &__select {
        padding: 1rem;
        border: 1px solid darken(v.$gris-claro, 28);
        border-radius: .5rem;

        &--file {
            border: none;
        }
    }

    &__texto {
        margin-top: 3rem;
    }

    &__imagen {
        max-width: 20rem;
    }

    &__listado {
        @include m.tags;
    }

    &__submit {
        @include m.boton(v.$naranja, v.$blanco);
        width: auto;
        align-self: flex-start;

        &--registrar {
            width: 100%;
        }
    }
}
